.memo-container {
    float: left;
    min-width: 200px;
    min-height: 250px;
    margin: 0;
    padding: 0 0 26px;
    overflow: hidden;
    font-size: 1em;
    box-shadow: 5px 5px 20px rgba(0, 0, 0, 0.2);
    background: #f3c93e;
    border: 1px solid #ccc;
    box-sizing: content-box;
    resize: both;
    position: absolute;    
  }

  .menu {    
    height: 25px;
    padding: 0 10px;
    border-bottom: 1px solid #ccc;
    text-align: center;
  }

  .menu > .date {
    line-height: 25px;    
    font-size: 1rem;
    color:midnightblue;   
  }

  .memo-text-area {
    position: relative;
    width: 100%;
    height: 100%;
    box-sizing: border-box;
    border: none;
    resize: none;
    background: #f3efbe;
    margin: 0;
    user-select: auto;
    padding: 10px;
  }